.hero {
    background: $dark-grey-2;
    padding: 130px 0 40px;
    min-height: 615px;

    &.topbar-active { 
        padding-top: 180px;
    }
    
    @include media-breakpoint-up(md) {
        padding: 150px 0 60px;

        &.topbar-active { padding-top: 200px; }
    }
    @include media-breakpoint-up(lg) {
        padding: 180px 0;
        min-height: 650px;

        &.topbar-active { padding-top: 230px; }
    }
    @include media-breakpoint-up(xl) {
        min-height: 745px;
    }
    @include media-breakpoint-up(xxl) {
        min-height: 800px;
    }

    &--article {
        overflow: hidden;
        padding: 120px 0 180px;

        @include media-breakpoint-up(lg) {
            padding: 180px 0;
            height: auto;
            min-height: 800px;
        }
    }
}

.hero__overlay {
    background: linear-gradient(to bottom, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.1) 30%), linear-gradient(to right, rgba(0,0,0,0.6) 0%, rgba(0,0,0,0.4) 100%);
    animation: fadeOut 500ms 1 forwards;
    transition: background $smooth 500ms;
    
    @include media-breakpoint-up(lg) {
        background: linear-gradient(to bottom, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.05) 35%), linear-gradient(to right, rgba(0,0,0,0.75) 0%, rgba(0,0,0,0.1) 43%);
    }
    
    &.is-darkened {
        background: linear-gradient(to bottom, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.25) 30%), linear-gradient(to right, rgba(0,0,0,0.6) 0%, rgba(0,0,0,0.4) 100%);
        @include media-breakpoint-up(lg) {
            background: linear-gradient(to bottom, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.2) 35%), linear-gradient(to right, rgba(0,0,0,0.75) 0%, rgba(0,0,0,0.25) 43%);
        }
    }

    &.is-opaque {
        background: $dark-grey-2;
    }
    &.is-active {
        animation: fadeIn 500ms 1 forwards;
    }
}

.hero__img {
    opacity: .85;
    max-height: 100%;
    will-change: transform;
}

.hero__video::-webkit-media-controls-start-playback-button {
    display: none;
}

.hero__content {
    animation: fadeOut 700ms $smooth 1 forwards;
    z-index: 1;
    padding: 0 30px;
    max-width: 500px;

    &.is-post,
    &.is-location {
        max-width: 650px;
    }

    @include media-breakpoint-up(lg) {
        padding: 0;
        left: 80px;
        margin-left: calc((100vw - 960px) / 2);
    }
    @include media-breakpoint-up(xl) {
        margin-left: calc((100vw - 1140px) / 2);
    }
    @include media-breakpoint-up(xxl) {
        margin-left: calc((100vw - 1440px) / 2);

        &.is-post {
            left: 137px;
        }
        &.is-memb-plan {
            left: 190px;
        }
    }

    &.is-active {
        animation: fadeIn 700ms $smooth 1 forwards;
    }

    &__headings {
        margin-bottom: 37px;
        @include media-breakpoint-up(lg) {
            margin-bottom: 50px;
        }
        
        h1 {
            font-size: 41px;
            line-height: 42px;   
            @include media-breakpoint-up(lg) {
                font-size: 65px;
                line-height: 70px;   
            }
        }

        .coming-soon {
            font-size: 22px;
            line-height: 23px;
            letter-spacing: .5px;
            margin-top: 20px;
            margin: 18px 0;
            
            @include media-breakpoint-up(lg) {
                font-size: 30px;
                line-height: 32px;
                margin-top: 22px;
                margin-bottom: 25px;
            }
        }
    }

    &__text {
        margin-top: -25px;
        margin-bottom: 25px;

        .is-location & {
            max-width: 500px;
        }

        * {
            color: $white;
        }
    }

    .selectric {
        min-width: 100%;
        @include media-breakpoint-up(lg) {
            min-width: 300px;
        }
    }

    &.no-video .selectric {
        @include media-breakpoint-down(md) {
            margin-top: 30px;
        }
    }

}

.hero__watch-video {
    width: 100%;
    margin-bottom: 54px;
    padding: 9px 18px;
    
    span {
        font-size: 16px;
        letter-spacing: 1px;
    }

    .icon img {
        width: 100%;
        height: 100%;
    }
}

.hero__video-close {
    display: none;
    position: absolute;
    z-index: 2;
    top: 18px;
    right: 15px;
    
    &.is-active {
        display: block;
    }

    img {
        width: 40px;
        height: 40px;
    }
}

.hero__back-link {
    font-size: 14px;
    line-height: 1;
    letter-spacing: 1.5px;
    margin-bottom: 20px;

    @include media-breakpoint-up(xxl) {
        position: absolute;
        top: 12px;
        left: -127px;
        
        &--memb-plan {
            left: -180px;
        }
    }
}

.hero__find-location {
    margin-top: 32px;
    cursor: pointer;

    img {
        width: 16px;
        @include media-breakpoint-up(lg) {
            width: 21px;
        }
    }

    span {
        font-size: 15px;
        line-height: 25px;

        @include media-breakpoint-up(lg) {
            font-size: 19px;
            line-height: 30px;
        }
    }
}

.hero__view-location {
    width: 100%;
    padding: 12px 18px;

    span {
        font-size: 16px;
    }
}

.hero__buttons {
    margin: 12px 0 0;
    @include media-breakpoint-up(lg) {
        margin: 70px 0 0;
        min-width: 650px;

    }
    .c-btn:nth-of-type(1) {
        margin-bottom: 30px;

        @include media-breakpoint-up(lg) {
            margin-right: 40px;
            margin-bottom: 0;
        }
    }
}

.hero__contact-info {
    margin-top: -10px;
    @include media-breakpoint-up(lg) {
        margin-top: -20px;
    }
    
    h4 {
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 1.3px;
        margin-bottom: 10px;
        
        @include media-breakpoint-up(lg) {
            font-size: 20px;
            margin-bottom: 15px;
        }
    }
    
    p, a {
        font-size: 16px;
        line-height: 28px;
        color: $white;
        margin-bottom: 0;

        @include media-breakpoint-up(lg) {
            font-size: 18px;
        }
    }

    a {
        text-decoration: underline;
    }

    .socials {
        a {
            width: 40px;
            height: 40px;
            margin-right: 21px;
            
            @include media-breakpoint-up(lg) {
                margin-right: 25px;
                width: 47px;
                height: 47px;
            }
        }
    }
}

.hero__sharing {
    position: absolute;
    left: calc(50% - 22px);
    transform: translateX(-50%);
    top: 197px;
    
    &__inner {
        width: 30px;

        .links {
            .link {
                margin-bottom: 15px;
                width: 20px;
            }
        }

        .line {
            height: 61px;
            width: 1px;
            background: $white;
            margin-bottom: 19px;
        }

        p {
            font-size: 13px;
            transform: rotate(-90deg);
            width: 55px;
            letter-spacing: 1.4px;
        }
    }
}

.hero__scroll-down {
    position: absolute;
    bottom: -19px;
    width: 20px;
    left: 30px;

    &--article {
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);
    
        @include media-breakpoint-up(lg) {
            bottom: 70px;
        }
    }
}